@import '../../../../pages/color-palette.scss';

section {
  //--| display: grid; |--//
  div[class*='skills'] {
    //--| Desktop (Landscape) -  |--//
    @media screen and (orientation: landscape) {
      & {
        //--| display: grid; |--//
        background: $red-neon;
      }
    }
    //--| Mobile (Portrait) -  |--//
    @media screen and (orientation: portrait) {
      & {
        //--| display: grid; |--//
        background: $blue-neon;
      }
    }
  }
}
